.profile__name {
	font-weight: 500;
	font-size: 42px;
	line-height: 48px;
	margin: 0;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}

@media screen and (max-width: 1023px) {
	.profile__name {
		font-size: 32px;
		line-height: 38px;
	}
}

@media screen and (max-width: 767px) {
	.profile__name {
		font-size: 27px;
		line-height: 33px;
		padding-left: 26px;
	}
}